import request from '../interceptor.js';

export function register(username, password, phone, trueName) {
  return request({
    url: '/rc-analyse/user/registered',
    method: 'post',
    data: {
      username,
      password,
      phone,
      trueName
    }
  });
}

export function login(username, password) {
  return request({
    url: '/rc-analyse/user/login',
    method: 'post',
    data: {
      username,
      password
    }
  });
}

export function logout(token) {
  return request({
    url: '/rc-analyse/user/loginout',
    method: 'get',
    params: {
      token
    }
  });
}

export function getRCUrl(token) {
  return request({
    url: '/rc-analyse/user/getRCUrl',
    method: 'get',
    params: {
      token
    }
  });
}

export function findPassword(username = '') {
  return request({
    url: '/api/user/findPassword',
    method: 'post',
    timeout: 20000,
    data: {
      username
    }
  });
}

/**
 * 更换密码
 *
 * @export
 * @param {string} [oldPass=''] 旧密码
 * @param {string} [newPass=''] 新密码
 * @returns
 */
export function changePass(oldPass = '', newPass = '') {
  return request({
    url: '/api/user/changePassword',
    method: 'post',
    data: {
      oldPass,
      newPass
    }
  });
}

export function getGroupUserInfo(modulename) {
  return request({
    url: `/rc-analyse/Query/GroupUserList/${modulename}`,
    method: 'get'
  });
}

export function saveWarningInfo(modulename, warningType, groupUserDetail) {
  return request({
    url: 'rc-analyse/save/warningInfo',
    method: 'post',
    data: {
      modulename,
      warningType,
      groupUserDetail
    }
  });
}
