import request from '@/interceptor.js';

/**
 * 获取产品名称列表
 *
 * @export
 * @returns promise
 */
export function getRCProductList() {
  return request({
    method: 'get',
    url: '/rc-analyse/query/rcproduct/name'
  });
}

/**
 * 获取样本数据列表
 *
 * @export
 * @param {string} [moduleName='']  样本名称
 * @param {number} [page=1]  当前页
 * @param {number} [size=10]  每页显示的总条数
 * @returns promise
 */
export function getSampleList(moduleName = '', page = 1, size = 10) {
  return request({
    method: 'post',
    url: `/rc-analyse/query/datasource?page=${page}&size=${size}`,
    data: {
      moduleName
    }
  });
}

/**
 * 导入样本数据(Excel)
 *
 * @export
 * @param {string} [name=''] required 样本名称
 * @param {string} [description=''] required 样本说明
 * @param {*} [file={}] required 文件对象
 * @returns promise
 */
export function importSample(name = '', description = '', file = {}) {
  const formdata = new FormData();
  formdata.append('name', name);
  formdata.append('description', description);
  formdata.append('file', file);
  return request({
    method: 'post',
    url: '/rc-analyse/upload/datasource',
    headers: {
      'Content-Type': 'multipart/form-data'
    },
    data: formdata
  });
}

