/**
 * 更新样本库接口
 *
 * @export
 * @param {string} [id=''] 样本Id
 * @param {*} moduletype 模块类型 0：授信，1：前置，2：评分卡
 * @param {*} rcProductName 产品名称
 * @param {*} use 状态 true正常 false 停用
 * @returns promise
 */
export function updateSample(id = '', moduletype, rcProductName, use) {
  return request({
    method: 'post',
    url: '/rc-analyse/update/datasource',
    data: {
      id,
      moduletype,
      rcProductName,
      use
    }
  });
}

