import {initAR} from '@/assets/scripts/getSomeSetting.js';

export default {
  //获取未读信息数量
  GET_UNREAD_MSG_NUMBER(state, result) {
    state.unReadMsgNumber = result.unReadMsgNumber; // 当日通过率
  },
  // 未读消息--->已读，数量会减少
  REDUCE_UNREAD_NUMBER(state, result) {
    if (state.unReadMsgNumber > 0) {
      state.unReadMsgNumber--;
    }
  },
  SHOW_LOADING(state) {
    state.isLoading = true;
  },
  CLOSE_LOADING(state) {
    state.isLoading = false;
  },

  UPDATE_RCPRODUCT_LIST(state, result = []) {
    state.rcproductList = result;
  },
  UPDATE_VAR_TYPE(state, newV) {
    state.varType = newV;
  },
  UPDATE_ANALYSISRESULT(state, obj) {
    state.analysisResult[obj.key] = obj.value;
  },
  RESET_ANALYSISRESULT(state) {
    state.analysisResult = initAR();
  },
  UPDATE_MONITORPAGE(state, obj) {
    state.monitorPage[obj.key] = obj.value;
  },
  UPDATE_SAMPLE(state, obj) {
    state.sample = obj;
  },
  USERINFO(state, user = {}) {
    state.user = user;
  },
  UPDATE_TREEDATALIST(state, arr) {
    state.treeDataList = arr;
  },
  UPDATE_TREECONTRACT(state, newV) {
    state.treeConstract = newV;
  },
  UPDATE_CONCTRASTDATERANGE(state, newV) {
    state.contractDateRange = newV;
  },
  UPDATE_MONITORLISTPARAMS(state, obj) {
    Object.assign(state.monitorListParams, obj);

  }
};
