import request from '@/interceptor.js';

/**
 * 获取消息列表
 *
 * @export
 * @param {number} [page=1] 当前页
 * @param {number} [size=10] 每页显示的总条数
 * @returns promise
 */
export function getMessageList(page = 1, size = 10, productName, variableKey, productReportName) {
  return request({
    method: 'get',
    url: `/rc-analyse/query/messagecenter`,
    params: {
      page,
      size,
      productName,
      variableKey,
      productReportName
    }
  });
}

/**
 * 查询未读消息数量
 *
 * @export
 * @returns promise
 */
export function getNoreadCount() {
  return request({
    method: 'get',
    url: '/rc-analyse/query/messagecenter/noreadcount'
  });
}

/**
 * 更新未读消息状态
 *
 * @export
 * @param {string} [id='']
 * @returns
 */
export function updateMessageStatus(id = '') {
  return request({
    method: 'get',
    url: `/rc-analyse/update/messagecenter/status/${id}`
  });
}
