import Vue from 'vue';

let filters = {
  samplesType(v) {
    if (v === 1) {
      return '样本';
    } else if (v === 2) {
      return '决策模块';
    }
    return v;
  },

  samplesSource(v) {
    if (v === 1) {
      return 'Excel';
    } else if (v === 2) {
      return 'RC';
    }
    return v;
  },
  analyseType(v) {
    let name = v;
    switch (v) {
      case 0:
        name = '单变量分析';
        break;
      case 1:
        name = '多变量分析';
        break;
      case 2:
        name = '变量波动监控';
        break;
      case 3:
        name = '决策过程监控';
        break;
      default:
        break;
    }
    return name;
  },
  tableKeyToValue(v) {
    if (v === 'key') {
      return '变量范围';
    } else if (v === 'value') {
      return '结果值';
    }
    return v;
  },
  moduleType(v) {
    if (v === 0) {
      return '授信';
    } else if (v === 1) {
      return '前置';
    } else if (v === 2) {
      return '评分卡';
    }
    return v;
  },
  isuse(v) {
    return v ? '使用' : '停用';
  },
  fieldtype(v) {
    if (v === 'float') {
      return '连续';
    } else if (v === 'text') {
      return '离散';
    }
    return v;
  }
};

Object.keys(filters).forEach(k => Vue.filter(k, filters[k]));
