import {getRCProductList} from '@/api/sample.js';
import {login, register, logout, getRCUrl} from '@/api/user.js';

export default {
  //获取未读信息数量
  getUnReadMsgNumber: ({commit, state}, payload) => {
    console.log(payload);
    commit('GET_UNREAD_MSG_NUMBER', payload);
  },

  // 未读消息--->已读，数量会减少
  reduceUnReadNumber: ({commit, state}) => {
    commit('REDUCE_UNREAD_NUMBER');
  },

  getRCProductList: ({commit}) => {
    getRCProductList()
      .then(result => {
        if (Array.isArray(result.result)) {
          commit('UPDATE_RCPRODUCT_LIST', result.result);
          // this.productList = result.result;
          return;
        }
      })
      .catch(err => console.log(err));
  },

  LOGIN({commit}, userInfo) {
    return new Promise((resolve, reject) => {
      login(userInfo.username.trim(), userInfo.password.trim())
        .then(result => {
          window.sessionStorage.setItem('TOKEN', result.result);
          commit('USERINFO', {
            username: userInfo.username,
            token: result.result
          });
          resolve(result);
        })
        .catch(err => {
          reject(err);
        });
    });
  },
  REGISTER({commit}, userInfo) {
    return new Promise((resolve, reject) => {
      register(userInfo.username.trim(), userInfo.password.trim(), userInfo.phone.trim(), userInfo.trueName.trim())
        .then(result => {
          commit('USERINFO', {
            username: userInfo.username,
            trueName: userInfo.trueName
          });
          resolve(result);
        })
        .catch(err => {
          reject(err);
        });
    });
  },
  LOGOUT({commit}, token) {
    return new Promise((resolve, reject) => {
      logout(token)
        .then(result => {
          commit('USERINFO', {
            username: '',
            token: ''
          });
          resolve(result);
        })
        .catch(err => reject(err));
    });
  },
  getRCUrl({commit}, token) {
    return new Promise((resolve, reject) => {
      getRCUrl(token)
        .then(result => {
          resolve(result);
        })
        .catch(err => reject(err));
    });
  }
};
