import axios from 'axios';
import app from '@/main.js';
import {list2, list3} from '@/assets/scripts/connect.list';

const CancelToken = axios.CancelToken;
const pending = []; // 声明一个数组用于存储每个ajax请求的取消函数和ajax标识
const removePending = (config = {}, f) => {
  let url = config.url.split('?')[0].replace(/(.+)\/rc-analyse(.+)/, '/rc-analyse$2');
  let i = pending.indexOf(url);
  if (i > -1) {
    if (typeof f === 'function') {
      f();
    } else {
      pending.splice(i, 1);
    }
  } else {
    pending.push(url);
  }
};

let baseURL = process.env.VUE_APP_BASE_API;

const request = axios.create({
  baseURL,
  timeout: 10000
});

request.defaults.retry = 2;
request.defaults.retryDelay = 1000;

// 加载进度设置
let requestingCount = 0;
const handleRequestLoading = () => {
  requestingCount++;
  if (requestingCount > 0) {
    app.$store.commit('SHOW_LOADING');
  }
};
const handleResponseLoading = () => {
  requestingCount--;
  if (requestingCount <= 0) {
    app.$store.commit('CLOSE_LOADING');
  }
};

request.interceptors.request.use(
  config => {
    handleRequestLoading();
    let url = config.url.split('?')[0].replace(/(.+)\/rc-analyse(.+)/, '/rc-analyse$2');
    // 判断该请求是不是在list2列表中；
    let isExist = list2.some(item => item.indexOf(url) > -1);
    if (!isExist) {
      // TODO 取消请求
      config.cancelToken = new CancelToken(c => {
        removePending(config, c);
      });
    }

    // 判断该请求是不是需要添加token；
    let isNotNeedToken = list3.some(item => item.indexOf(url) > -1);
    if (!isNotNeedToken) {
      config.headers.Authorization = app.$store.state.user.token;
    }
    return config;
  },
  error => {
    console.log(error.config);
    handleResponseLoading();
    return Promise.reject(error);
  }
);

// Add a response interceptor
request.interceptors.response.use(
  response => {
    handleResponseLoading();
    removePending(response.config);
    const res = response.data;
    if (res.statusCode !== 0) {
      if (String(res.statusCode) === '401') {
        app.$message.warning(res.statusDescription);
        app.$router.push('/login');
        return res;
      }
      // 不需要在拦截器提示的，params参数里有interceptorHint信息，且等于needless
      if (!(response.config.params && response.config.params.interceptorHint === 'needless')) {
        if (res.statusDescription) {
          app.$message.warning(res.statusDescription);
        }
      }
      return Promise.reject(res);
    }
    return res;
  },
  error => {
    if (axios.isCancel(error)) {
      handleResponseLoading();
      console.log('Request canceled');
    } else {
      const config = error.config;
      removePending(config);
      console.log(error);
      if (error.response && String(error.response.status) === '401') {
        handleResponseLoading();
        app.$message.warning(error.response.statusText);
        app.$router.push('/login');
        return error;
      }
      if (!config || !config.retry) {
        handleResponseLoading();
        app.$message.warning(error.response.statusText);
        return Promise.reject(error);
      }
      config.__retryCount = config.__retryCount || 0;
      if (config.__retryCount >= config.retry) {
        handleResponseLoading();
        app.$message.warning(error.response.statusText);
        return Promise.reject(error);
      }
      config.__retryCount += 1;
      const backoff = new Promise(resolve => {
        setTimeout(() => {
          handleResponseLoading();
          app.$nextTick(() => {
            resolve();
          });
        }, config.retryDelay || 1);
      });

      return backoff.then(() => {
        return request(config);
      });
    }
    return Promise.reject(error);
  }
);

export default request;
