router.beforeEach((to, from, next) => {
  NProgress.start();
  if (to.meta && Array.isArray(to.meta.crumbs) && to.meta.crumbs.length > 0 && to.meta.crumbs.slice(0).pop()) {
    document.title = to.meta.crumbs.slice(0).pop().value;
  }
  next();
});

router.afterEach((to, from) => {
  NProgress.done();
  NProgress.remove();
  // 不是去往变量分析页面，删除变量分析结果
  if (to.meta.parentMenu !== 'VA') {
    app.$store.commit('RESET_ANALYSISRESULT');
  }
  if (from.name === 'univariate-monitor-view' || from.name === 'univariate-monitor') {
    app.$store.commit('UPDATE_MONITORPAGE', {
      key: 'univariate',
      value: 'form'
    });
  }
  if (from.name === 'decision-monitor-view' || from.name === 'decision-monitor') {
    app.$store.commit('UPDATE_MONITORPAGE', {
      key: 'decision',
      value: 'form'
    });
  }
});

export default router;
